import React, { Component, Fragment } from 'react';
import { Input, Modal, Button, Radio, Checkbox, Icon, Row, Col } from 'antd';

class InventoryAdd extends Component {

    state = {
        visible: false,
        name: "",
        temperature: 0,
        humidity: 0,
        illuminance: false,
        oximeter: true,
        explosion: false,
        plainOptions: ['', '', '']
    };

    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    handleOk = e => {
        console.log(e);
        this.setState({
            visible: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            visible: false,
        });
    };

    nameChange = (name) => {
        this.setState({
            name: name.target.value
        })
    }

    temperatureChange = (temperature) => {
        this.setState({
            temperature: temperature.target.value
        })
    }

    humidityChange = e => {
        this.setState({ humidity: e.target.value });
        console.log(e.target.value)
    };

    onChange = (checkedValues) => {
        console.log('checked = ', checkedValues);
        if (checkedValues.some(x => x === this.state.plainOptions[0])) {
            this.setState({
                illuminance: true
            })
        } else {
            this.setState({
                illuminane: false
            })
        }

        if (checkedValues.some(x => x === this.state.plainOptions[1])) {
            this.setState({
                oximeter: true
            })
        } else {
            this.setState({
                oximeter: false
            })
        }

        if (checkedValues.some(x => x === this.state.plainOptions[2])) {
            this.setState({
                explosion: true
            })
        } else {
            this.setState({
                explosion: false
            })
        }

    }

    submit = () => {
        this.props.addInventory(
            this.state.name, this.state.temperature, this.state.humidity, this.state.illuminance, this.state.oximeter, this.state.explosion
        )
        this.handleOk()
    }


    render() {


        return (
            <span>
                <Button type="primary" onClick={this.showModal} size="small" icon="plus" shape="circle" />
                <Modal
                    title="  "
                    visible={this.state.visible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                >
                    <Row style={{ marginBottom: 10 }}>
                        <Col span={6} >

                        </Col>
                        <Col span={18}>

                        </Col>
                    </Row>

                    <Row style={{ marginBottom: 10 }}>
                        <Col span={6} >
                             :
                        </Col>
                        <Col span={18}>
                            <Input placeholder=" " onChange={this.nameChange} />
                        </Col>
                    </Row>

                    <Row style={{ marginBottom: 10 }}>
                        <Col span={6} >
                             :
                        </Col>
                        <Col span={18}>
                            <Input placeholder=" " onChange={this.temperatureChange} />
                        </Col>
                    </Row>

                    <Row style={{ marginBottom: 10 }}>
                        <Col span={6} >
                             :
                        </Col>
                        <Col span={18}>
                            <Radio.Group value={this.state.humidity} onChange={this.humidityChange}>
                                <Radio.Button value={-1}>(~3%)</Radio.Button>
                                <Radio.Button value={0}></Radio.Button>
                                <Radio.Button value={1}>(71%~)</Radio.Button>
                            </Radio.Group>
                        </Col>
                    </Row>

                    <Row style={{ marginBottom: 10 }}>
                        <Col span={6} >
                             :
                        </Col>
                        <Col span={18}>
                            <Checkbox.Group options={this.state.plainOptions} defaultValue={['']} onChange={this.onChange} />
                        </Col>
                    </Row>

                    <div style={{ marginBottom: 10 }}>
                        <center><Button onClick={this.submit} >  </Button></center>
                    </div>
                </Modal>
            </span>
        );
    }

}

export default InventoryAdd;
