import React, { Component } from 'react';
import { Col, Button, message } from 'antd';
import { Modal } from 'antd';
import { getUser, getLab } from '../authentication';
import StockInfoAlarm from './StockInfoAlarm.js'
import { serverUrl } from '../setting'

class AlarmInfo extends Component {

  state = {
    visible: false
  }

  constructor(props) {
    super(props);
  }

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };


  handleRemove = () => {
    //    onRemove  id  
    const { info, onRemove } = this.props;
    onRemove(info.id, info.alarmType);
    this.deleteFetch();
  }

  deleteFetch = () => {
    fetch(serverUrl + '/alarm/' + getUser().id +'/'+ this.props.info.alarmType + '/' + this.props.info.id,  {
      method: 'DELETE', //'GET', 'POST', 'DELETE' 
      headers: { 'Content-Type': 'application/json' } // 
    }).then(response => {
      if (response.status === 200) {
        //   
      } else {
        //   ->    
      }
    })
  }
  

  getMessage = () => {
    const { info, onRemove } = this.props;
    if (info.alarmType === 1) {
      if (this.props.info.date > 0) {
        return (

          <div style={{ fontSize: 16 }}>
            <Button type="link" onClick={this.showModal} style={{ fontSize: '18px' }}><b>{this.props.info.name}</b></Button>
            <br /> <b>{this.props.info.date}</b> .
          <span style={{ marginLeft: 10 }}><Button icon="close" onClick={this.handleRemove}></Button></span>

            <Modal
              title=" "
              visible={this.state.visible}
              onOk={this.handleOk}
              onCancel={this.handleCancel}
            >
              {/*    */}
              <StockInfoAlarm stock={this.props.info.stockInfo} changeVolume={this.changeVolume} deleteStock={this.deleteStock}
                changeInventory={this.changeInventory} />
            </Modal>
          </div>
        )
      }
      else if (this.props.info.date === 0){
        return (

          <div style={{ fontSize: 16 }}>
            <Button type="link" onClick={this.showModal} style={{ fontSize: '18px' }}><b>{this.props.info.name}</b></Button>
            <br /> <b></b> .
          <span style={{ marginLeft: 10 }}><Button icon="close" onClick={this.handleRemove}></Button></span>

            <Modal
              title=" "
              visible={this.state.visible}
              onOk={this.handleOk}
              onCancel={this.handleCancel}
            >
              {/*    */}
              <StockInfoAlarm stock={this.props.info.stockInfo} changeVolume={this.changeVolume} deleteStock={this.deleteStock}
                changeInventory={this.changeInventory} />
            </Modal>
          </div>
        )
      }
      else {
        return (

          <div style={{ fontSize: 16 }}>
            <Button type="link" onClick={this.showModal} style={{ fontSize: '18px' }}><b>{this.props.info.name}</b></Button>
            <br /> <b>.</b>
            <span style={{ marginLeft: 10 }}><Button icon="close" onClick={this.handleRemove}></Button></span>

            <Modal
              title=" "
              visible={this.state.visible}
              onOk={this.handleOk}
              onCancel={this.handleCancel}
            >
              {/*    */}
              <StockInfoAlarm stock={this.props.info.stockInfo} changeVolume={this.changeVolume} deleteStock={this.deleteStock}
                changeInventory={this.changeInventory} />
            </Modal>
          </div>
        )
      }
    }
    else if (info.alarmType === 2) {
      if (this.props.info.volume !== 0) {
        return (

          <div style={{ fontSize: 16 }}>
            <Button type="link" onClick={this.showModal} style={{ fontSize: '18px' }}><b>{this.props.info.name}</b></Button>
            <br/> : <b>{this.props.info.place}</b>
            <br/>   .
            <span style={{marginLeft : 10}}>  <Button icon="close" onClick={this.handleRemove}></Button></span>

            <Modal
              title=" "
              visible={this.state.visible}
              onOk={this.handleOk}
              onCancel={this.handleCancel}
            >
              {/*    */}
              <StockInfoAlarm stock={this.props.info.stockInfo} changeVolume={this.changeVolume} deleteStock={this.deleteStock}
                changeInventory={this.changeInventory} />
            </Modal>
          </div>
        )
      }
      else{
        return (

          <div style={{ fontSize: 16 }}>
            <Button type="link" onClick={this.showModal} style={{ fontSize: '18px' }}><b>{this.props.info.name}</b></Button>
            <br/> : <b>{this.props.info.place}</b>
            <br/> .
            <span style={{marginLeft : 10}}>   <Button icon="close" onClick={this.handleRemove}></Button></span>

            <Modal
              title=" "
              visible={this.state.visible}
              onOk={this.handleOk}
              onCancel={this.handleCancel}
            >
              {/*    */}
              <StockInfoAlarm stock={this.props.info.stockInfo} changeVolume={this.changeVolume} deleteStock={this.deleteStock}
                changeInventory={this.changeInventory} />
            </Modal>
          </div>
        )
      }
    }
    else {
     
      return (

        <div style={{ fontSize: 16 }}>
          <Button type="link" onClick={this.showModal} style={{ fontSize: '18px' }}><b>{this.props.info.name}</b></Button>
          <br />  <b>{this.props.info.period}</b> .
            <br />    .
            <span style={{ marginLeft: 10 }}><Button icon="close" onClick={this.handleRemove}></Button></span>

          <Modal
            title=" "
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
          >
            {/*    */}
            <StockInfoAlarm stock={this.props.info.stockInfo} changeVolume={this.changeVolume} deleteStock={this.deleteStock}
              changeInventory={this.changeInventory} />
          </Modal>

        </div>
      )
    }
  }

  

  render() {

    return (
      <div >
        {this.getMessage()}
      </div>
    );
  }
}

export default AlarmInfo;
