import React, { Component } from 'react';
import { Form, Icon, Input, Button, message, Modal } from 'antd';
import { Link } from "react-router-dom";
import { Typography } from 'antd';
import { getUser, getLab } from '../authentication';
import { history } from '../History';
import { serverUrl } from '../setting';
import { login } from '../authentication';

const { Title } = Typography;

class MyGroupRegister extends Component {

  state = { visible: false };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log(values)
        const registInformation = {
          name: values.groupName,
          password: values.password,
        }
        fetch(serverUrl + '/lab/' + getUser().id, {
          method: 'PUT',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(registInformation)
        }).then(response => {
          if (response.status === 200) {
            //    
            message.success('Lab    !');
            response.json().then(response => {
              localStorage.setItem('lab', JSON.stringify(response.lab));
              this.props.afterGroupGenerate(getUser().id);
            })
          } 
          else if (response.status === 400) {
            //   
            message.warning('   Lab  !');
          }
          else {
            //  
            message.warning('  !');
          }
        })
      } else {
      }
    });
  };

  //        true
  validateToNextPassword = (rule, value, callback) => {
    const { form } = this.props;
    form.validateFields(['confirm'], { force: true });
    callback();
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form onSubmit={this.handleSubmit} className="form">
        <center><font size='4' >my Lab  </font></center><br/>
        {/*  */}
        <Form.Item> 
          {getFieldDecorator('groupName', {
            rules: [{ required: true, message: '   !' }],
          })(
            <Input
              prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
              placeholder="my Lab Name"
            />,
          )}
        </Form.Item>

        {/*  */}
        <Form.Item hasFeedback>
          {getFieldDecorator('password', {
            rules: [
              {
                required: true, message: '   !',
              },
              {
                validator: this.validateToNextPassword,
              },
            ],
          })(<Input.Password
            prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
            placeholder="my Lab Password" />)}
        </Form.Item>

        {/*   */}
        <Form.Item>
          <center>
          <Button type="primary" htmlType="submit" className="button" style={{width: 80}}>
             
          </Button>
          </center>
        </Form.Item>
      </Form>
    );
  }
}

export default Form.create()(MyGroupRegister);
