import React, { Component } from 'react';
import { Form, Icon, Input, Button, Modal, message } from 'antd';
import { Link } from "react-router-dom";
import { Typography } from 'antd';
import { getUser, getLab } from '../authentication';
import { history } from '../History';
import { serverUrl } from '../setting';
import { Alert } from 'antd';



const { Title } = Typography;

class MyGroupInvite extends Component {

  state = { visible: false };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  
  

  searchMember = (email) => {
    console.log(3)
    console.log(email)
    fetch(serverUrl + '/member/' + email, {
      method: 'GET', //'GET', 'POST', 'DELETE' 
      headers: { 'Content-Type': 'application/json' }, // 
    }).then(response => {
      if (response.status === 200) {
        //   

        response.json().then(response => {
          console.log(response) //       
          var a = response.member
          var userId = a.id
          var labId = getLab().id

          fetch(serverUrl + '/lab/' + labId + '/' + userId, {
            method: 'PUT', //'GET', 'POST', 'DELETE' 
            headers: { 'Content-Type': 'application/json' }, // 
          }).then(response => {
            if (response.status === 200) {
              //   
              message.success(' !');
            } else {
              console.log('fetch error')
              message.warning('    Lab !');
            }
          }).then(response => {

          })

        })
      } else {
        console.log('fetch error')
        message.warning('     !');
      }
    }).then(response => { })
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
        if (!err) {
          console.log(values)
          //values.email
          this.searchMember(values.email)
        }
      }
    )
  }


  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <div style={{ margin: '10px 0' }}>
        <center>
          <Icon type="user-add"   style={{ fontSize: 33 }} />
          <Title style={{ marginBottom: 30 }}><font size='4' font color='black'>   </font></Title>
        </center>

        <Form onSubmit={this.handleSubmit} className="form">
          {/*  */}
          <Form.Item>
            {getFieldDecorator('email', {
              rules: [{ required: true, message: '   !' }],
            })(
              <Input
                prefix={<Icon type="mail" style={{ color: 'rgba(0,0,0,.25)' }} />}
                placeholder="Email"
              />,
            )}
          </Form.Item>
          <Form.Item>
            {/*<Link to='/login/forgot' className="login-form-forgot">Forgot password</Link>*/}
            <center>
            <Button type="primary" htmlType="submit" className="button" style={{width: 80}}>
              
            </Button>
            </center>
            
          </Form.Item>
        </Form>

                
               
               
               
               
                
                
                {/* 
                <Form.Item>
                    {getFieldDecorator('memberName', {
                        rules: [{ required: true, message: '    !' }],
                    })(
                        <Input
                            //prefix={<Icon type="mail" style={{ color: 'rgba(0,0,0,.25)' }} />}
                            placeholder="Member Email"
                        />,
                    )}
                </Form.Item>
                */}


            </div>
        );
    }
}

export default Form.create()(MyGroupInvite);
