import React, { Component } from 'react';
import { Modal, Form, Button, Icon } from 'antd';
import { Typography } from 'antd';
import { history } from '../History';
import { getUser, getLab } from '../authentication';
import { serverUrl } from '../setting'

const { Title } = Typography;
const { Text } = Typography;

class MyGroupWithdraw extends Component {

  state = { visible: false };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
    this.withdrawGroup();
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  constructor(props) {
    super(props);

  }


  withdrawGroup = () => {

    fetch(serverUrl + '/lab/' + getLab().id + '/' + getUser().id, { // uri 
      method: 'DELETE', //'GET', 'POST', 'DELETE' 
      headers: { 'Content-Type': 'application/json' }, // 
    }).then(response => {
      if (response.status === 200) {
        console.log(11112222)
        localStorage.removeItem('lab')
        this.props.afterGroupWithdraw()

      } else {
        //   ->    
      }
    }).then(response => {})
  }

  render() {
    //const { getFieldDecorator } = this.props.form;
    return (
      <div style={{ margin: '10px 0' }}>
      <center><Icon type="rocket"   style={{ fontSize: 33 }}  /><Title style={{marginBottom : 30}}><font size='4' font color='black'>my Lab </font></Title></center>

      <font size='3' >  my Lab  :  
      </font><font size='3' font color='black'> {getLab().name} </font>
      <br /><br/><font size='3' >  my Lab   :  
      </font><font size='3' font color='black'> {this.props.count} </font>

      <br />
      <br />
      <br />
      <center>
      <Button type="primary" onClick={this.showModal} style={{width: 80}}>
      
      </Button>
      </center>
      
      <Modal
      title="  "
      visible={this.state.visible}
      onOk={this.handleOk} 
      onCancel={this.handleCancel}
      >
      <p><center><font size='3' color='black'><b> ?</b></font></center></p>
      <p><center><font size='2' color='black'>  ,      !</font></center></p>

      </Modal>

      </div>
    );
  }
}

export default Form.create()(MyGroupWithdraw);
