import React, { Component } from 'react';
import { Modal, Button, Input, Form, Select, message, InputNumber, Row, Col, Icon } from 'antd';

import ChemicalInfo from './ChemicalInfo';
import SelectInventory from './SelectInventory';
import { serverUrl } from '../setting';
import { getUser } from '../authentication';

const { Search } = Input;


const { Option } = Select;

//     (my Lab  )
class ChemicalAdd extends Component {

    state = {
        visible: false,
        chemical: {},
        nickname: "default",
        suggest: [],
        notSuggest: [],
        selectedInventory: null,
        number: 0,
        unit: 'g',
        expire: '',
        nicknameCheck: true
    }

    constructor(props) {
        super(props);
    }

    showModal = () => {
        this.setState({
            visible: true
        });
    };

    handleCancel = e => { // 
        this.setState({
            visible: false,
        });
    };

    nickNameChange = (e) => {
        this.setState({
            nickname: e.target.value,
            nicknameCheck: false
        })
        if (e.target.value === '') {
            this.setState({
                nicknameCheck: true
            })
        }
    }

    setExpire = (e) => {
        this.setState({
            expire: e.target.value
        })
    }

    search = (chemicalName) => {
        chemicalName = chemicalName.replace(' ', '_')
        console.log(chemicalName)
        // fetch 
        const url = serverUrl + '/chemical/info/' + getUser().id
        fetch(url, { // uri 
            method: 'POST', //'GET', 'POST', 'DELETE' 
            headers: { 'Content-Type': 'application/json' }, // 
            body: JSON.stringify({
                name: chemicalName
            }) // body 
        }).then(response => {
            if (response.status === 200) {
                //   
                response.json().then(response => {
                    console.log(23123)
                    console.log(response)
                    // response   state     
                    // response    
                    this.setState({
                        chemical: response.chemical
                    }, () => {
                        this.getInventorySuggestList()
                    })
                })
            } else {
                message.error('   !');
                //   ->    
            }
        })

    }

    getInventorySuggestList = () => {
        //   
        // fetch
        const url = serverUrl + '/chemical/' + getUser().id + '/' + this.state.chemical.id
        fetch(url, { // uri 
            method: 'GET', //'GET', 'POST', 'DELETE' 
            headers: { 'Content-Type': 'application/json' }, // 
        }).then(response => {
            if (response.status === 200) {
                //   
                return response.json()
            } else {
                //   ->    
            }
        }).then(response => {
            // response   state     
            // response    
            console.log(response) //       
            //
            this.setState({
                suggest: response.suggest,
                notSuggest: response.notSuggest
            })
            if (response.suggest.length === 0) {
                message.warning('  .       !')
            }
        })

    }

    resetState = () => {
        this.setState({
            visible: false,
            chemical: {},
            nickname: "default",
            suggest: [],
            notSuggest: [],
            selectedInventory: null,
            number: 0,
            unit: 'g',
            expire: '',
            nicknameCheck: true
        })
    }

    //   
    checkExpire = () => {
        const date = this.state.expire
        if (date.length != 6) {
            return false;
        }
        var dateInt = parseInt(date);
        if (dateInt < 100101 || dateInt > 999999) {
            return false;
        }

        const year = parseInt(date.substring(0, 2)) + 2000
        const month = parseInt(date.substring(2, 4))
        if (month < 1 || month > 12) {
            return false;
        }
        const day = parseInt(date.substring(4, 6))
        if (day < 1 || day > 31) {
            return false;
        }
        try {
            var realDate = new Date()
            realDate.setFullYear(year)
            realDate.setMonth(month)
            realDate.setDate(day)
        } catch (e) {
            return false;
        }
        return true
    }

    selectInventory = (inventoryId) => {
        // fetch
        this.setState({
            selectInventory: inventoryId
        })
        console.log(inventoryId)
        // state  
        var gram = this.state.number
        if (this.state.unit === 'mL') {
            gram *= this.state.chemical.density
        }

        //this.state.expire 6 
        //TODO : expire  

        if (this.state.nicknameCheck) {
            //  

            if (this.checkExpire()) {

                if (this.state.number < 0.01) {
                    message.error('   !')
                } else {
                    //  inventory list inventoryId  
                    var finish = false
                    for (var i = 0; i < this.state.suggest.length; i++) {
                        if (this.state.suggest[i].id === inventoryId) {
                            finish = true;
                            this.handleCancel()
                            this.resetState()
                            this.props.addChemical(this.state.chemical, inventoryId, gram, this.state.expire, this.state.nickname, this.state.suggest[i].name)
                        }
                    }
                    if (!finish) {
                        for (var i = 0; i < this.state.notSuggest.length; i++) {
                            if (this.state.notSuggest[i].id === inventoryId) {
                                this.handleCancel()
                                this.resetState()
                                this.props.addChemical(this.state.chemical, inventoryId, gram, this.state.expire, this.state.nickname, this.state.notSuggest[i].name)
                            }
                        }
                    }
                }


            } else {
                message.error('    !')
            }

        } else {
            message.error('    !')

        }
        // nickname 
        /*
        const nicknameUrl = serverUrl + '/chemical/nickname/' + getUser().id
        fetch(nicknameUrl, { // uri 
            method: 'POST', //'GET', 'POST', 'DELETE' 
            headers: { 'Content-Type': 'application/json' }, // 
            body: JSON.stringify({
                nickname : this.state.nickname
            }) // body 
        }).then(response => {
            if( response.status === 200){
                this.props.addChemical(this.state.chemical, inventoryId, gram, this.state.expire, this.state.nickname)
            } else {
                message.error(' ')
            }
        })
        */
    }

    nicknameCheck = () => {
        const nicknameUrl = serverUrl + '/chemical/nickname/' + getUser().id
        fetch(nicknameUrl, { // uri 
            method: 'POST', //'GET', 'POST', 'DELETE' 
            headers: { 'Content-Type': 'application/json' }, // 
            body: JSON.stringify({
                nickname: this.state.nickname
            }) // body 
        }).then(response => {
            if (response.status === 200) {
                message.success('  ')
                this.setState({
                    nicknameCheck: true
                })
                //this.props.addChemical(this.state.chemical, inventoryId, gram, this.state.expire, this.state.nickname)
            } else {
                message.error('  .   ')
                this.setState({
                    nicknameCheck: false
                })
            }
        })
    }



    handleNumberChange = e => {
        console.log(e)
        const number = parseFloat(e).toFixed(2);
        if (isNaN(number)) {
            return;
        }
        if (!('value' in this.props)) {
            this.setState({ number });
        }
        this.triggerChange({ number });
    };

    handleUnitChange = unit => {
        if (!('value' in this.props)) {
            this.setState({ unit });
        }
        this.triggerChange({ unit });
    };


    triggerChange = changedValue => {
        // Should provide an event to pass value to Form.
        const { onChange } = this.props;
        if (onChange) {
            onChange({
                ...this.state,
                ...changedValue,
            });
        }
        console.log(this.state)
    };


    render() {
        const { size } = this.props;
        const { unit, number } = this.state;

        return (

            <span>
                <center>
                    {/*  */}
                    <Button Button type="primary" shape="round" onClick={this.showModal} style={{ fontSize: '12px' }} size="large" >   </Button>
                    {/*   */}
                    <Modal
                        title=" "
                        visible={this.state.visible}
                        onOk={this.handleCancel}
                        onCancel={this.handleCancel} // 
                    >
                        {/*   */}
                        <div>
                            <Search
                                placeholder="    "
                                enterButton="Search"
                                size="middle"
                                onSearch={value => this.search(value)}
                            />
                        </div>

                        {/*    */}
                        <div>
                            <ChemicalInfo chemical={this.state.chemical} />
                        </div>

                        {/*   */}
                        <div style={{ marginTop: 10 }}>
                            {/*    */}
                            <div style={{ marginBottom: 10 }}>
                                <Row>
                                    <Col span={16} >
                                        <center>
                                            <Input placeholder="   " onChange={this.nickNameChange} />
                                        </center>
                                    </Col>
                                    <Col span={8} >
                                        <center>
                                            <Button onClick={this.nicknameCheck}></Button>
                                        </center>
                                    </Col>
                                </Row>
                            </div>

                            {/*   &   */}
                            <div style={{ marginBottom: 10 }}>
                                <Row>
                                    <Col span={16} >
                                        <center>
                                            <InputNumber
                                                type="text"
                                                size={size}
                                                value={number}
                                                onChange={this.handleNumberChange}
                                                style={{ width: '95%' }}
                                            />

                                        </center>
                                    </Col>
                                    <Col span={8} >
                                        <center>
                                            <Select
                                                value={unit}
                                                size={size}
                                                style={{ width: '100%' }}
                                                onChange={this.handleUnitChange}
                                            >
                                                <Option value="g">g</Option>
                                                <Option value="mL">mL</Option>
                                            </Select>

                                        </center>
                                    </Col>
                                </Row>
                            </div>

                            <div style={{ marginBottom: 10 }}>
                                <center>


                                </center>
                            </div>

                            <div style={{ marginBottom: 10 }} >
                                <Input placeholder="   (YYMMDD)" onChange={this.setExpire} />
                            </div>


                            {/*  */}
                            <div style={{ marginBottom: 10 }}>
                                <SelectInventory suggest={this.state.suggest} notSuggest={this.state.notSuggest} selectInventory={this.selectInventory} chemical={this.state.chemical} />
                            </div>

                        </div>

                    </Modal>
                </center>
             
            </span>
        );
    }
}

export default ChemicalAdd;